body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.header-component {
    background-color: #737b8b;
    color: #fff;
    font-size: calc(8px + 2vmin);
    padding: 5px;
    text-transform: lowercase;
    position: fixed;
    width: 100%;
    z-index: 1;
}

.header-component::first-letter {
    text-transform: capitalize;
}

.loader {
    position: fixed;
    top: 96px;
}

.main-container {
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    font-size: calc(8px + 2vmin);
    color: rgb(24, 24, 54);
    padding: 16px;
}

.main-container ul {
    margin-top: 16px;
}

.App-link {
    color: #737b8b;
}

.movies-list {
    max-width: 970px;
}

.movies-list li {
    border-bottom: 1px solid #eaeaea;
}

#alert-dialog-title h2 {
    color: #700000;
}

#form-dialog-title h2 {
    color: #1e2e85;
}

.movies-list li span.jss85 {
    color: #0e5173!important;
}

.add-fab {
    position: fixed!important;
    bottom: 5%;
    left: 4%;
    z-index: 1;
}

.icons-wrap {
    display: -webkit-flex;
    display: flex;
    width: 110px;
}

.MuiListItemText-root-62:first-child {
    width: 100%;
}

@media(max-width:500px) {
    .movies-list li {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
    }

    .MuiTypography-subheading-85,
    .movies-list li span.jss85 {
        min-width: 73vw;
    }

    .add-fab {
        left: 80%;
    }
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

   
